.carrousel {
  &__wrapper {
    height: 100vh;
    overflow: hidden;
  }

  &__image {
    background-size: cover !important;
  }
}

.artist-name {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 400px;
    height: 160px;
    -webkit-transform: translate(-50%, -50%);  
    transform: translate(-50%, -50%);   
    border: 2px solid white;

    &__wrapper {
      border: 2px solid white;
      height: 121px;
      margin: 6px;
      text-align: center;
      color: #fff;
      font-size: 40px;
      padding: 21px 70px 0px 70px;
      text-transform: uppercase;
    }

}

